/*
 * 1977
 *
 */

._1977 {
  position: relative;
  filter: contrast(1.1) brightness(1.1) saturate(1.3);
}


._1977 img {
  width: 100%;
  z-index: 1;
}


._1977:before {
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  pointer-events: none;
  z-index: 2;
}


._1977:after {
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  pointer-events: none;
  z-index: 3;
}


._1977:after {
  background: rgba(243, 106, 188, 0.3);
  mix-blend-mode: screen;
}

/*
 * Moon
 *
 */

.moon {
  position: relative;
  filter: grayscale(1) contrast(1.1) brightness(1.1);
}


.moon img {
  width: 100%;
  z-index: 1;
}


.moon:before {
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  pointer-events: none;
  z-index: 2;
}


.moon:after {
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  pointer-events: none;
  z-index: 3;
}


.moon::before {
  background: #a0a0a0;
  mix-blend-mode: soft-light;
}


.moon::after {
  background: #383838;
  mix-blend-mode: lighten;
}

* {
  box-sizing: border-box;
  outline: none;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none;
}

ul {
  list-style: none;
}

ul li {
  display: inline-block;
}

body, html {
  background: white;
  color: #333;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  background: #1C1C1C;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.ico-email {
  width: 1.4em;
  height: 1em;
  fill: #FFF;
}

button {
  -webkit-apperance: none;
  border: 0;
  box-shadow: none;
}

button:focus {
  outline: none;
}

.btn {
  display: inline-block;
  position: relative;
  text-align: center;
  cursor: pointer;
  font-size: 1.4rem;
  transition: all 0.25s ease-in-out;
}

.btn:focus, .btn:active {
  outline: none;
}

.btn--block {
  display: block;
  width: 100%;
}

.btn-line span {
  position: relative;
  z-index: 2;
}

.btn-line::after {
  display: inline-block;
  content: ' ';
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  left: 50%;
  right: 50%;
  background: currentColor;
  height: 2px;
  transition: all 0.25s ease-in-out;
}

.btn-line:hover::after {
  left: -5%;
  right: -5%;
}

.icon {
  display: inline-block;
  font-style: normal;
  content: ' ';
}

.ico {
  fill: currentColor;
  transition: all 0.25s ease-in-out;
}

.intro {
  position: relative;
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  padding: 0 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
}

.intro__description {
  display: flex;
  max-width: 800px;
  margin: auto;
  opacity: 0;
}

.intro__control {
  width: 33.3%;
  flex: 0 1 33.3%;
  padding: 20px;
  text-align: center;
  font-size: 16px;
  line-height: 1.5;
}

.intro__cta {
  position: absolute;
  bottom: 5vh;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
}

.grid-container {
  cursor: grabbing;
}

.grid-transition {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0px;
  height: 0px;
  background: #000;
  border-radius: 100%;
  transition: all 0.5s ease;
}

.grid-transition.active {
  width: 200vw;
  height: 200vw;
}

.profile {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80vw;
  height: 100vh;
  margin: 0 auto;
}

@media (max-width: 970px) {
  .profile {
    display: block;
    width: 90vw;
  }
}

.back-to-grid, .back-home {
  position: absolute;
  top: 3%;
  right: 4%;
  color: #FFF;
  z-index: 5000;
  text-shadow: 1px 1px #333333;
  transition: color 1s ease;
}

.back-to-grid:hover, .back-home:hover {
  color: #CC208E;
  text-decoration: none;
}

.profile p {
  color: #6D6875;
}

.imgWithFilter {
  width: 50%;
  height: 500px;
  opacity: 0;
  transform: translateX(25%);
}

@media (max-width: 970px) {
  .imgWithFilter {
    margin-top: 150px;
    width: 100%;
    height: auto;
  }
}

.imgWithFilter img {
  max-width: 100%;
  max-height: 100%;
  display: block;
  margin: 0 auto;
}

.imgWithFilter canvas {
  width: 500px;
}

@media (max-width: 970px) {
  .imgWithFilter canvas {
    display: block;
    width: 300px;
    margin: 0 auto;
  }
}

.userInfo {
  width: 50%;
  font-family: 'Roboto Condensed', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.userInfo .info-container {
  width: 60%;
}

@media (max-width: 970px) {
  .userInfo .info-container {
    width: 100%;
    margin: 15px;
    margin-top: 80px;
  }
}

.userInfo h1 {
  color: #CC208E;
  font-size: 3rem;
}

.userInfo h3 {
  font-family: 'Abel', sans-serif;
  font-size: 1.5rem;
  font-weight: lighter;
  color: #6D6875;
}

.userInfo .info-item {
  margin-bottom: 20px;
  opacity: 0;
  transform: translateY(20%);
  font-weight: lighter;
}

.userInfo .linkedin-profile {
  display: inline-block;
  margin: 0;
  margin-top: 30px;
}

@media (max-width: 970px) {
  .userInfo {
    width: 100%;
  }
}

.filters {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.filters p {
  width: 100%;
  font-family: 'Roboto Condensed', sans-serif;
  color: #6D6875;
}

.filter {
  width: 25%;
  border: 5px solid transparent;
  position: relative;
}

.filter:hover {
  cursor: pointer;
}

.check-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  width: 60px;
  height: 60px;
  z-index: 8;
  background: transparent;
}

.chosen .check-icon {
  display: block;
}

.filters canvas, .filters img {
  width: 100%;
}

.login a {
  border: none;
  position: absolute;
  top: 3%;
  right: 2%;
  background-color: transparent;
  font-size: 1.2rem;
  transition: color 5s ease;
}

.login a:hover {
  color: #CC208E;
  background-image: none;
  background-color: transparent;
  box-shadow: none;
}

.form-container {
  display: flex;
  width: 80vw;
  margin: 0 auto;
  margin-top: 60px;
}

@media (max-width: 970px) {
  .form-container {
    width: 90vw;
    display: block;
  }
}

.image-and-filters {
  width: 50%;
  padding: 35px;
}

.image-and-filters .form-group {
  text-align: right;
  margin-right: 15%;
}

@media (max-width: 970px) {
  .image-and-filters .form-group {
    text-align: center;
    margin: 50px;
    margin-right: 0;
    margin-left: 0;
  }
}

@media (max-width: 970px) {
  .image-and-filters {
    width: 100%;
  }
}

.big-img {
  display: block;
  margin: 0 auto;
  margin-bottom: 35px;
  width: 70%;
}

#change-img {
  margin-top: 50px;
}

#change-img:focus {
  outline: none;
}

.form-control-file {
  display: none;
}

.change-img-label {
  padding: 10px;
  font-size: 1rem;
  display: inline-block;
  margin: 0 auto;
}

.change-img-label:hover img.upload-grey {
  display: none;
}

.change-img-label:hover img.upload-white {
  display: inline;
}

.change-img-label img {
  background: transparent;
}

.change-img-label img.upload-white {
  display: none;
}

.form-inner {
  width: 50%;
  padding: 35px;
}

@media (max-width: 970px) {
  .form-inner {
    width: 100%;
  }
}

form {
  width: 70%;
  margin: 0 auto;
}

@media (max-width: 970px) {
  form {
    width: 100%;
  }
}

.form-control {
  color: #CC208E;
  border-radius: 0;
  padding: 10px;
  background: none;
  border: 1px solid #6D6875;
}

.form-control:focus {
  border: 1px dotted #CC208E;
  color: #FFF;
  background: #000;
}

label {
  color: #6D6875;
  font-family: 'Roboto Condensed', sans-serif;
}

select {
  -webkit-appearance: none;
}

.button-container {
  text-align: right;
}

.btn.btn-secondary {
  font-size: 1rem;
  margin-top: 50px;
}
