
.grid {
  &-container {
    cursor: grabbing; }

  &-transition {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 0px;
    height: 0px;
    background: #000;
    border-radius: 100%;
    transition: all 0.5s ease;
    &.active {
      width: 200vw;
      height: 200vw; } } }
